.card {
    position: relative;
    max-width: 300px;
    margin: 20px;
    background: $white;
    box-shadow: 0px 0px 30px 2px #000;
    background-size: cover;
    height: 375px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .header {
        background-size: cover;
        min-height: 215px;
    }
    .content {
        padding: 2px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
    }
    .text {
        .food {
            color: $black;
            text-align: left;
            font-weight: normal;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            margin: 5px 10px;
        }
        .fa-clock-o {
            color: $l-blue;
            margin: 5px 10px;
            text-align: left;
        }
        .fa-fire {
            color: $fire-red;
            text-align: left;
        }
    }
   .stars {
        margin: 10px 10px;
        li a i {
            color: $star;
        }
    }
    .info {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin: 10px 10px;
        text-align: left;
        font-size: $font-sm;
    }
}