.recipeDetails {
    background: $white;
    .row {
        display: flex;
        flex-wrap: wrap;
        .cell {
            flex: 1;
            min-width: 300px;
        }
    }
    .header {
        height: 300px;
        background-position: center center;
        background-size: cover;
    }
    .content {
        padding: 2px;
        .details, .nutritionalFact {
            margin: 5px 30px;
        }
        .likes {
            .badge {
                position: absolute;
                top: 2px;
                font-size: 10px;
            }
        }
    }
    .footer {
        display: flex;
        justify-content: center;
        button {
            padding: 10px 20px;
        }
    }
    .text {
        .food {
            color: $black;
            text-align: left;
            font-weight: normal;
            text-transform: uppercase;
            letter-spacing: 0.1em;
            margin: 5px 30px;
        }
        .fa-clock-o {
            color: $l-blue;
            margin: 5px 30px;
            text-align: left;
        }
        .fa-fire {
            color: $fire-red;
            margin: 5px 30px;
            text-align: left;
        }
        .fa-heart {
            color: $pink;
            margin: 5px;
        }
        .description, .ingredients {
            margin: 5px 30px;
        }
    }
   .stars {
        margin: 10px 30px;
        li a i {
            color: $star;
        }
    }
    .info {
        margin: 10px 30px;
        text-align: left;
        font-size: $font-md;
    }
}